import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { Amplify, Auth } from 'aws-amplify';
import { AmplifyAuthenticator, AmplifySignUp } from '@aws-amplify/ui-react';
import awsconfig from '../../aws-exports';
import { AuthState } from '@aws-amplify/ui-components';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';

import './modal.css';
import './auth.css';

const signUpFields =
  [
    { type: 'email' },
    { type: 'password' }
  ]
;
Amplify.configure(awsconfig);

export default class SubmitOfferModal extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      authState: undefined
    };
  }

  componentDidUpdate (prevProps, prevState) {
    if (this.state.authState !== prevState.authState &&
      this.state.authState === AuthState.SignedIn) {
      this.props.handleSubmitOffer(this.props.propertyId, Auth);
    }
  }

  render () {
    return (
      <>
        <Modal
          size='xl'
          show={this.props.display === true}
          onHide={() => {
            this.props.onHandleHideConfirmModal();
          }}
          centered
        >
          <BlockUi
            blocking={this.props.submittingOffer === true}
            tag='div'
            z-index='1500'
            loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
          >
            <Modal.Header>
              <Modal.Title className='title2'>Submission</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              {
                this.state.authState !== AuthState.SignedIn &&
                  <AmplifyAuthenticator
                    usernameAlias='email' handleAuthStateChange={(nextAuthState, data) => {
                      this.setState(
                        { authState: nextAuthState }
                      );
                    }}
                  >
                    <AmplifySignUp formFields={signUpFields} slot='sign-up' usernameAlias='email' />
                  </AmplifyAuthenticator>
              }
              {
                (this.state.authState === AuthState.SignedIn && this.props.submittingOffer !== true) &&
                  <div className='submission-result'>
                    <a className='maos' href='../dashboard'>Go to Dashboard</a>{' '}to complete the submission.
                  </div>
              }
              {
                this.props.submittingOffer === true &&
                  <div className='submission-result'>
                    Submitting
                  </div>
              }
            </Modal.Body>
            <Modal.Footer>
              <Button
                variant='secondary'
                className='save_btn animated_fastest'
                onClick={() => {
                  this.props.onHandleHideConfirmModal();
                }}
              >
              Close
              </Button>
            </Modal.Footer>
          </BlockUi>
        </Modal>

      </>
    );
  }
}
