import React from 'react';
import axios from 'axios';
import imgProperty from '../../images/property.jpg';
import { HeaderProperty } from '../../components/Shared/Header_property';
import Footer from '../../components/Shared/Footer';
import SubmitOfferModal from '../../components/NileListedProperty/SubmitOfferModal';
import { toastr } from 'react-redux-toastr';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import numeral from 'numeral';
import './main.css';

export default class Dashboard extends React.Component {
  constructor (props) {
    super(props);

    this.handleOpenSubmitOffer = this.handleOpenSubmitOffer.bind(this);
    this.handleHideConfirmModal = this.handleHideConfirmModal.bind(this);
    this.SubmitOffer = this.SubmitOffer.bind(this);

    this.state = {
      LoadingPropertyList: undefined,
      PropertiesList: undefined,
      RequestSubmit: false,
      SubmittingOffer: undefined,
      RequestSubmitPropertyId: undefined,
      DisplayConfirmModal: undefined,
      DuplicatedSubmission: undefined,
      PropertyNotFound: undefined,
      OfferId: undefined,
      UserSubmissionError: undefined
    };
  }

  async componentDidMount () {
    this.setState({ LoadingPropertyList: true });
    const url = `${process.env.REACT_APP_RIVERDALE_PUBLIC_API}/listedproperties`;
    const requestConfig = {
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
        'X-Api-Key': process.env.REACT_APP_RIVERDALE_PUBLIC_API_KEY
      }
    };

    try {
      const response = await axios.get(url, requestConfig);
      this.setState({ PropertiesList: response.data.Properties });
    } catch (error) {
      this.setState({ propertyDetails: undefined });
      toastr.error('Loading properties', 'Sorry, something went wrong, please refresh the page and try again.');
    } finally {
      this.setState({ LoadingPropertyList: false });
    }
  }

  handleOpenSubmitOffer (propertyId) {
    this.setState({
      RequestSubmit: true,
      RequestSubmitPropertyId: propertyId
    });
  }

  async SubmitOffer (propertyId, auth) {
    const url = `${process.env.REACT_APP_RIVERDALE_BASE_URL}/digioffer/offers`;
    const requestConfig = {
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
        authorization: (await auth.currentSession()).getIdToken().getJwtToken()
      }
    };

    this.setState({ SubmittingOffer: true });
    var responseState = {
      RequestSubmitPropertyId: undefined,
      DuplicatedSubmission: undefined,
      PropertyNotFound: undefined,
      OfferId: undefined,
      UserSubmissionError: undefined
    };

    try {
      const response = await axios.post(url, { PropertyId: propertyId }, requestConfig);
      this.setState(
        {
          ...responseState,
          OfferId: response.data.OfferId
        }
      );
      toastr.success('Submit offer', 'Offer submitted.');
    } catch (error) {
      if (error.response !== undefined) {
        if (error.response.status === 404) {
          this.setState(
            {
              ...responseState,
              PropertyNotFound: true
            }
          );
          toastr.error('Submit offer', 'Sorry, the property does not exist or has been closed.');
        } else if (error.response.status === 409) {
          this.setState(
            {
              ...responseState,
              DisplayConfirmModal: true,
              DuplicatedSubmission: true
            }
          );
          toastr.success('Submit offer', 'Offer for this property has already been submitted.');
        } else {
          toastr.error('Submit offer', 'Sorry, something wrong.');
        }
      } else {
        toastr.error('Submit offer', 'Sorry, something wrong.');
      }
    } finally {
      this.setState({ SubmittingOffer: false });
    }
  }

  handleHideConfirmModal () {
    this.setState({ RequestSubmit: false });
  }

  render () {
    const properties = this.state.PropertiesList;
    if (!properties) {
      return null;
    }

    const requestSubmit = this.state.RequestSubmit;

    return (
      <>
        <HeaderProperty />
        <BlockUi
          blocking={this.props.LoadingPropertyList === true}
          tag='div'
          z-index='5000'
          loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
        >
          <div className='property-container'>
            <div className='property-list'>
              {
                properties.map(property =>
                  <span
                    key={property.PropertyId}
                    className='property-item'
                  >
                    <div className='t_img'>
                      <div
                        className='property_image' style={{
                          backgroundImage: `url(${imgProperty})`
                        }}
                      />
                    </div>
                    <div className='t_infoss'>
                      {property.StreetAddress} <br />
                      {property.City} {property.County} {property.State} {property.Zip9}<br />
                      {numeral(property.ListPrice).format('$0,0')}

                    </div>
                    <div
                      className='next_btn3 animated_fastest'
                      onClick={
                        (event) => { event.stopPropagation(); this.handleOpenSubmitOffer(property.PropertyId); }
                      }
                    >Submit
                    </div>
                  </span>
                )
              }
            </div>
          </div>
        </BlockUi>

        {requestSubmit === true &&
          <SubmitOfferModal
            display={requestSubmit}
            submittingOffer={this.state.SubmittingOffer}
            propertyId={this.state.RequestSubmitPropertyId}
            onHandleHideConfirmModal={() => this.handleHideConfirmModal()}
            handleSubmitOffer={this.SubmitOffer}
            onAuthenticated={this.handleAuthenticated}
          />}
        <Footer />
      </>
    );
  }
}
